import React from 'react';
import './Header.css';
import { HashLink as Link } from 'react-router-hash-link';

const delayBtn = () => {
  setTimeout(() => {
    const element1 = document.getElementById('btn-1');
    const element2 = document.getElementById('btn-2');
    element1.classList.remove('hidden');
    element2.classList.remove('hidden');
  }, 6000);
};

const Header = ({ onRouteChange }) => {
  return (
    <div className='h-screen background' id='home'>
      <div className='modal'>
        <p className='message'>Hello</p>
        <p className='message'>
          <span className='text_1'>Welcome to my portfolio</span>
          <span className='text_2'>Do you want to see more?</span>
        </p>
        <div className='options'>
          <Link to='/#about' className='mr-10'>
            <button id='btn-1' className='btn hover:bg-green-400'>
              Yes
            </button>
          </Link>
          <button
            id='btn-2'
            onClick={() => onRouteChange('signin')}
            className='btn hover:bg-pink-300'
          >
            No
          </button>
        </div>
      </div>
    </div>
  );
};

export default Header;
